.productCard {
  width: 292px;
  border-radius: 10px;
  z-index: 0;

  cursor: pointer;
  transition: 0.6s ease all;
}

.productCard:hover {
  transform: scale(1.1);
  z-index: 2000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.navLink-txtDecoration {
  text-decoration: none;
}

.productImage {
  height: 181px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.addToCartBtn {
  width: 203px;
}

.addToCartBtn:hover {
  color: #1c004a !important;
  background-color: #3ad8c1;
}

.text-blue {
  color: #2a8cbb;
}

.text-aqua {
  color: #3ad8c1;
}

.bg-purple-dark {
  background-color: #1c004a;
}

.letter-spacing {
  letter-spacing: 0.1rem;
}
